body {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

.people {
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  margin: 30px;
  display: grid;
}

.person {
  text-align: center;
  color: #2f4f4f;
  justify-content: center;
  width: 200px;
  height: 231px;
  font-size: 20px;
  line-height: 23px;
  transition: all 1s;
  position: relative;
}

img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin: 12px;
  transition: all 1s;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  line-height: 18px;
  position: absolute;
  top: 50%;
  right: 10px;
}

.sport, .pay {
  opacity: 0;
}

.person:hover {
  background-color: #eee;
  border-radius: 12px;
  width: 264px;
  height: 344px;
  transition: all 1s;
}

.person:hover img {
  border-radius: 60px;
  width: 240px;
  height: 240px;
  transition: all 1s;
}

.person:hover .sport, .person:hover .pay {
  opacity: 1;
}

@media only screen and (width <= 750px) {
  .people {
    grid-template-columns: repeat(2, 1fr);
    display: grid;
  }
}
